    --bg-tertiary: #202225;
    --bg-tertiary-alt: #18191c;

    --bg-blurple: hsl(235, 85.6%, 64.7%);
    --bg-blurple-hover: hsl(235, 51.4%, 52.4%);

    --bg-error: #f04444;
    --bg-success: #3ba55d;

    /* Text Colours */
    --colour-primary: #b9bbbe;
    --colour-normal: #dcddde;
    --colour-muted: #72767d;
