    color: rgba(255, 255, 255, var(--tw-text-opacity));
    margin-top: .75rem;
    background-color: var(--bg-success);
    font-family: var(--font-primary);
}

.modal-form-input-o {
    height: 2.5rem;
    border-radius: 0.25rem;
    border-width: 1px;
    --tw-border-opacity: 1;
    border-color: rgba(0, 0, 0, var(--tw-border-opacity));
    --tw-border-opacity: 0.3;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    outline: 2px solid transparent;
    outline-offset: 2px;
    width: 40rem;
    background-color: var(--bg-secondary);
    color: var(--colour-normal);
    font-weight: 100;
    font-family: var(--font-primary);
}

.modal-form-input-o::-webkit-input-placeholder {
    color: hsla(0, 0%, 100%, 0.3);
}

.modal-form-input-o:-ms-input-placeholder {
    color: hsla(0, 0%, 100%, 0.3);
}

.modal-form-input-o::placeholder {
    color: hsla(0, 0%, 100%, 0.3);
}

.modal-form-input-o:hover {
    --tw-border-opacity: 0.8;
}

.option-title {
    margin-bottom: 0.5rem;
    width: 100%;
    font-size: 0.875rem;
    line-height: 1.25rem;
    font-family: var(--font-display);
    color: #b9bbbe;
}
/* User Content: Messaging, Members, etc. */

.user-content {
    display: flex;
    flex-grow: 1;
    overflow: hidden;
}

.channel-name {
    position: fixed;
    z-index: 1;
    float: left;
    display: flex;
    height: 3rem;
    width: 100%;
    flex-direction: row;
    align-items: center;
    box-shadow: 0 1px 0 rgba(4,4,5,0.2),0 1.5px 0 rgba(6,6,7,0.05),0 2px 0 rgba(4,4,5,0.05);
}

.channel-info-name {
    margin-left: 1rem;
    margin-right: 1rem;
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
    font-family: var(--font-primary);
}

.chat {
    float: left;
    display: flex;
    flex-grow: 1;
    flex-direction: column-reverse;
    padding-top: 3rem;
}

.chat-bar-container {
    position: relative;
    display: flex;
    height: 90px;
    width: 100%;
    align-items: center;
    justify-content: center;
    padding-left: 16px;
    padding-right: 16px;
    min-height: 90px;
}

.chat-bar {
    position: relative;
    display: flex;
    height: 2.75rem;
    width: 100%;
    flex-direction: row;
    border-radius: 0.375rem;
    padding-left: 1rem;
    padding-right: 1rem;
}

.chat-bar-area {
    flex-grow: 1;
    border-width: 0px;
    background-color: transparent;
    outline: 2px solid transparent;
    outline-offset: 2px;
    font-family: var(--font-primary);
    font-weight: 400;
    color: var(--colour-normal);
}

.chat-bar-area::-webkit-input-placeholder {
    color: var(--colour-muted);
}

.chat-bar-area:-ms-input-placeholder {
    color: var(--colour-muted);
}

.chat-bar-area::placeholder {
    color: var(--colour-muted);
}

.chat-container {
    display: flex;
    flex-grow: 1;
    flex-direction: column-reverse;
    overflow-x: hidden;
    overflow-y: scroll;
}

.chat-item {
    margin-top: 0.5rem;
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    min-width: 100%;
}

.chat-item-small {
    margin-bottom: 1px;
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 0.25rem;
    padding-bottom: 0.25rem;
    padding-left: 72px;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    min-width: 100%;
}

.chat-item-text-small {
    max-width: 100%;
    font-size: 1rem;
    line-height: 1.5rem;
    word-wrap: break-word;
    line-height: 16px;
    font-family: var(--font-primary);
    color: var(--colour-normal);
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
}

.chat-item-small:hover {
    background-color: var(--bg-secondary);
}
.chat-item:hover {
    background-color: var(--bg-secondary);
}

.chat-item-picture {
    margin-right: 1rem;
    height: 2.5rem;
    width: 2.5rem;
    min-width: -webkit-max-content;
    min-width: max-content;
    border-radius: 9999px;
    min-width: 40px;
    min-height: 40px;
}

.chat-item-content {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.chat-user-info {
    margin-top: -4px;
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 0.5rem;
    line-height: 14px;
}

.chat-item-name {
    font-size: 1rem;
    line-height: 1.5rem;
    font-weight: 700;
    --tw-text-opacity: 1;
    color: rgba(255, 255, 255, var(--tw-text-opacity));
    font-family: var(--font-primary);
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
}

.chat-item-date {
    font-size: 0.75rem;
    line-height: 1rem;
    color: var(--colour-muted);
    font-family: var(--font-primary);
}

.chat-item-text {
    max-width: 100%;
    font-size: 1rem;
    line-height: 1.5rem;
    word-wrap: break-word;
    line-height: 16px;
    font-family: var(--font-primary);
    color: var(--colour-normal);
    -webkit-user-select: text;
        -ms-user-select: text;
            user-select: text;
}

::-webkit-scrollbar {
    width: 16px;
    height: 16px;
}

::-webkit-scrollbar-thumb, ::-webkit-scrollbar-track {
    border: 4px solid transparent;
    background-clip: padding-box;
    border-radius: 8px;

}

::-webkit-scrollbar-thumb {
    background-color: #202225;
}

::-webkit-scrollbar-track {
    background-color: hsl(210,calc(1*9.8%),20%);
    background-color: hsl(210,calc(var(--saturation-factor, 1)*9.8%),20%);
    margin-bottom: -1rem;
}

::-webkit-scrollbar-corner {
    background-color: transparent;
}

.member-list {
    position: relative;
    float: right;
    margin-top: 3rem;
    display: flex;
    width: 15rem;
    min-width: -webkit-max-content;
    min-width: max-content;
    flex-direction: column;
    gap: 1.5rem;
    padding-top: 1.25rem;
    height: calc(100% - 48px);
    font-family: var(--font-display);
}

.member-list-category {
    display: flex;
    cursor: pointer;
    flex-direction: column;
    align-items: center;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 150ms;
    transition-duration: 150ms;
    transition-timing-function: cubic-bezier(0.4, 0, 1, 1);
}

.member-list-name {
    margin-left: 1.5rem;
    display: flex;
    width: 100%;
    flex-direction: row;
    align-items: center;
    font-size: 0.75rem;
    line-height: 1rem;
}

.members {
    margin-top: auto;
    display: flex;
    width: 100%;
    flex-direction: column;
    gap: 0.25rem;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
    padding-top: 0.25rem;
}

.member-profile-image {
    margin-left: 0.5rem;
    height: 2rem;
    width: 2rem;
    border-radius: 9999px;
    --tw-bg-opacity: 1;
    background-color: rgba(239, 68, 68, var(--tw-bg-opacity));
}

.member {
    display: flex;
    height: 2.75rem;
    width: 100%;
    flex-direction: row;
    align-items: center;
    border-radius: 0.25rem;
    font-family: var(--font-primary);
}

.member-name {
    margin-left: 0.5rem;
}

.member:hover {
    background-color: rgba(79,84,92,0.32);
}

.member:hover .member-name {
    --tw-text-opacity: 1;
    color: rgba(220, 221, 222, var(--tw-text-opacity));
}
